import {ApiPutCancelOrder,ApiPutOrderReceive} from '@/plugins/api/order'
import UiButton from '@/components/UiButton.vue';
export default {
	components: { UiButton },
	props : {
		visible : {
			type : Boolean,
			default : false
		},
		orderId : {
			type : Number
		}
	},
	data(){
		return {
			selected : '',
			cancelData : [
				{value : 1, label : '我不想买了'},
				{value : 2, label : '地址信息填写错误'},
				{value : 3, label : '商品降价'},
				{value : 4, label : '商品无货'},
				{value : 5, label : '其他'}
			]
		}
	},
	computed:{
		myVisible : {
			get(){
				return this.visible;
			},
			set(val){
				this.$emit('update:visible', val)
			}
		}
	},
	methods:{

		async cancelOrder(){
			const ooderId = this.orderId;
			const {error, result} = await ApiPutCancelOrder({
				cancelReasonType : this.selected,
				orderId : ooderId
			});
			if(error){
				this.$message.error(error.message);
				return false;
			}
			this.myVisible = false;
			this.$emit('cancel')
			// 取消成功，跳到订单关闭页
		}
	}
}
